import React from "react";

const letters = [
  "أَلِف",
  "وَاو",
  "دَال",
  "ذَال",
  "رَا",
  "زَاي",
  "بَا",
  "تَا",
  "ثَا",
  "نُون",
  "يَاء",
  "جِيم",
  "حَا",
  "خَا",
  "سِين",
  "شِين",
  "صَاد",
  "ضَاد",
  "طَا",
  "ظَا",
  "عَيْن",
  "غَيْن",
  "فَا",
  "قَاف",
  "كَاف",
  "لَام",
  "مِيم",
  "هَا",
  "هَمزة",
  "أَلفِ مَقْصٌورَة",
  "تَاء مَرْبُوطَة",
  "لَام ألف",
];

const pages = [
  2, 4, 6, 8, 10, 12, 16, 18, 20, 22, 24, 26, 28, 30, 32, 34, 36, 38, 40, 42,
  44, 46, 48, 50, 52, 54, 56, 58, 62, 66, 68, 70,
];

function IndexButton({ onClick, className }) {
  return (
    <div className={"index-btn" + className} onClick={onClick}>
      <span></span>
      <span></span>
      <span></span>
    </div>
  );
}

export default function IndexPage({ setIndexopen, onPageChange }) {
  return (
    <div
      className="overlay"
      id="overlay"
      onClick={(e) => {
        if (e.target.id === "overlay") setIndexopen(false);
      }}
    >
      <div className="index-page">
        <div className="header">
          <h1>الفِهْرِس</h1>
          <h5>Index Page</h5>
        </div>
        <div className="container">
          {letters.map((letter, key) => {
            return (
              <div className="letter-box" key={key}>
                <div
                  className="letter"
                  onClick={() => {
                    onPageChange(pages[key]);
                    setIndexopen(false);
                  }}
                >
                  {letter}
                  <div className="page-num">صـP. {pages[key]}</div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}

export { IndexButton };
