import React from "react";

export default function CharPage({ words, number, title, font, color, file }) {
  let letterAudio;
  if (file) {
    letterAudio = new Audio(`./sounds-albayan-2/${file}.mp3`);
  }

  return (
    <div className="char-page">
      <div className="row">
        <div
          className="char-title"
          dangerouslySetInnerHTML={{ __html: title.colorRedWord(font) }}
          onClick={() => {
            if (letterAudio) {
              letterAudio.play();
            }
          }}
        ></div>
        <div className={"num"}>{`${number}`}</div>
      </div>

      <div className="words-container">
        {words.map((word, key) => {
          let audio = new Audio(`./sounds-albayan-2/${word.file}.mp3`);
          return (
            <div className="word-container" key={key}>
              <div className="word">
                <div className="floating-letter">
                  <span
                    dangerouslySetInnerHTML={{
                      __html: word.letter.colorWord(font),
                    }}
                  >
                    {}
                  </span>
                </div>
                <span className="wrapper">
                  <div
                    className="actual-word"
                    dangerouslySetInnerHTML={{
                      __html: word.word.colorRedWord(font),
                    }}
                    onClick={() => {
                      audio.play();
                    }}
                  ></div>
                </span>
              </div>
              <div className="image">
                <img
                  src={`./imgs-albayan-2/${word.file}.png`}
                  key={key + new Date().toString()}
                  alt={word.english}
                  loading="lazy"
                />
                <span className="en-word eng">{word.english}</span>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
