import { useState } from "react";
import IndexPage, { IndexButton } from "./IndexPage";

export default function Page({
  children,
  color = "red",
  onPageChange,
  pageNumber,
}) {
  let [indexopen, setIndexopen] = useState(false);
  let [teacherNote, setTeacherNote] = useState(false);
  return (
    <div className={"page " + `${color}`}>
      {indexopen ? (
        <IndexPage setIndexopen={setIndexopen} onPageChange={onPageChange} />
      ) : (
        ""
      )}
      {teacherNote ? (
        <div
          className="overlay"
          onClick={(e) => {
            if (e.target.className === "overlay") setTeacherNote(false);
          }}
        >
          <div className="box">
            <h1>خَاص بِالْمُعَلِّم</h1>
            <h1>For Teacher</h1>
            <h2>
              الألف لا تأتي في بداية الكلمة مفردة فهي إما همزة وصل وإما همزة
              قطع، مثال (آدَم) للتقريب فقط.
            </h2>
            <h2 dir="ltr">
              Alif does not come at the beginning of a words as Alif Maad, it
              coems as Hamzah-Tul-Wasel or Hammzah-Tul-Qata'. The example (آدَم)
              is to show similarity.
            </h2>
          </div>
        </div>
      ) : (
        ""
      )}
      <div className="container">
        {pageNumber < 3 && pageNumber > 0 ? (
          <div
            className="teacher-btn"
            onClick={() => {
              setTeacherNote(true);
            }}
          >
            !
          </div>
        ) : (
          ""
        )}
        <IndexButton
          className={indexopen ? " open" : ""}
          onClick={() => {
            setIndexopen(!indexopen);
          }}
        />

        <div className="header">
          <h1>
            الْبَيَانُ <span className="red">(2)</span>
            <span className="eng italic"> Al-Bayan</span>
          </h1>
        </div>
        {children}
      </div>
    </div>
  );
}
