import "react-app-polyfill/ie9";
import "react-app-polyfill/stable";
import React from "react";
import { BrowserRouter } from "react-router-dom";
import App from "./src-albayan-2/App";

import { createRoot } from "react-dom/client";

String.prototype.toArabicNumbers = function () {
  let id = ["۰", "۱", "۲", "۳", "۴", "۵", "۶", "۷", "۸", "۹"];
  return this.replace(/[0-9]/g, function (w) {
    return id[+w];
  });
};

String.prototype.colorRedWord = function (name = "red") {
  return this.replace(/؟(.*?)؟/g, function (w) {
    return `<span class="${name}">${w.slice(1, w.length - 1)}</span>`;
  });
};

String.prototype.parseTagColor = function () {
  return this.replace(/؟(.*?)؟/g, function (w) {
    return `<span class="colored">${w.slice(1, w.length - 1)}</span>`;
  });
};

String.prototype.colorWord = function (name = "red") {
  return `<span class="${name}">${this}</span>`;
};

const container = document.getElementById("root");
const root = createRoot(container);
root.render(
  <BrowserRouter>
    <App />
  </BrowserRouter>
);
