import { useState } from "react";
import { useSearchParams } from "react-router-dom";
import "./main.scss";
import Page from "./Page.jsx";
import SpecialPage from "./SpecialPage.jsx";
import CharPage from "./CharPage.jsx";
import NavButton from "./NavButton.jsx";
import data from "./data";
import LettersPage from "./LettersPage.jsx";
import ExamplePage from "./ExamplePage.jsx";
import AllLettersPage from "./AllLettersPage.jsx";
function Book() {
  let [searchParams, setSearchParams] = useSearchParams();

  let pageUrl = Number.parseInt(searchParams.get("page"));
  let [page, setPage] = useState(
    pageUrl <= data.length && pageUrl > 0 ? pageUrl - 1 : 0
  );

  function updatePage(counter) {
    setPage(page + counter);
    setSearchParams({ page: page + counter + 1 });
  }

  function goToPage(p) {
    p--;
    setPage(p);
    setSearchParams({ page: p + 1 });
  }

  return (
    <Page color={data[page].color} onPageChange={goToPage} pageNumber={page}>
      {page !== 0 ? (
        <NavButton
          dir="right"
          onClick={() => {
            if (page !== 0) updatePage(-1);
          }}
        />
      ) : (
        ""
      )}

      {data[page].type === 1 ? (
        <LettersPage {...data[page]} />
      ) : data[page].type === 2 ? (
        <CharPage {...data[page]} />
      ) : data[page].type === 3 ? (
        <ExamplePage {...data[page]} />
      ) : data[page].type === 4 ? (
        <AllLettersPage {...data[page]} />
      ) : data[page].type === 5 ? (
        <SpecialPage {...data[page]} />
      ) : (
        ""
      )}

      {page !== data.length - 1 ? (
        <NavButton
          onClick={() => {
            if (page + 1 !== data.length) updatePage(1);
          }}
        />
      ) : (
        ""
      )}
      <h2 className="page-number">- {`${page + 1}`} -</h2>
    </Page>
  );
}

function App() {
  return <Book />;
}

export default App;
