import React from "react";

export default function ExamplePage({ color, font, file, english, text }) {
  let audio = new Audio(`./sounds-albayan-2/${file}.mp3`);
  return (
    <div className="example-page">
      <div className="image">
        <img src={`./imgs-albayan-2/${file}.png`} alt={english} />
      </div>
      <div className="eng eng-description">{english}</div>
      <div className="text">
        <div
          className="words"
          dangerouslySetInnerHTML={{ __html: text.colorRedWord(font) }}
          onClick={() => {
            if (audio) audio.play();
          }}
        ></div>
      </div>
    </div>
  );
}
