import React from "react";

export default function LettersPage({ letters, font, note, files }) {
  let lettersCon;

  function Letter({ data, font = "red" }) {
    let audio;
    if (data.file) {
      audio = new Audio(`./sounds-albayan-2/${data.file}.mp3`);
    }
    return (
      <div
        className="letter"
        onClick={() => {
          if (audio) {
            audio.play();
          }
        }}
      >
        <div className="box">
          <span className={font}>{data.letter}</span>
        </div>
      </div>
    );
  }

  let rows = [];
  let sounds = [];

  if (letters.length === 6) {
    for (let i = 0; i < letters.length; i += 2) {
      rows.push(letters.slice(i, i + 2));
    }
    for (let i = 0; i < letters.length; i += 2) {
      sounds.push(files.slice(i, i + 2));
    }

    lettersCon = (
      <div className="letters-container">
        {rows.map((row, k) => (
          <div className="row" key={k}>
            {row.map((letter, key) => (
              <Letter data={{ letter, file: sounds[k][key] }} key={key} />
            ))}
          </div>
        ))}
      </div>
    );
  } else if (letters.length === 10) {
    rows.push(letters.slice(0, 3));
    rows.push(letters.slice(3, 5));
    rows.push(letters.slice(5, 8));
    rows.push(letters.slice(8, 11));

    sounds.push(files.slice(0, 3));
    sounds.push(files.slice(3, 5));
    sounds.push(files.slice(5, 8));
    sounds.push(files.slice(8, 11));

    lettersCon = (
      <div className="letters-container coll">
        {rows.map((row, k) => (
          <div className={"row" + (row.length === 2 ? " two" : "")} key={k}>
            {row.map((letter, key) => (
              <Letter
                data={{ letter, file: sounds[k][key] }}
                font="green"
                key={key}
              />
            ))}
          </div>
        ))}
      </div>
    );
  } else {
    for (let i = 0; i < letters.length; i += 3) {
      rows.push(letters.slice(i, i + 3));
    }
    for (let i = 0; i < letters.length; i += 3) {
      sounds.push(files.slice(i, i + 3));
    }

    lettersCon = (
      <div className="letters-container coll">
        {rows.map((row, k) => (
          <div className={"row"} key={k}>
            {row.map((letter, key) => (
              <Letter
                data={{ letter, file: sounds[k][key] }}
                font="green"
                key={key}
              />
            ))}
          </div>
        ))}
      </div>
    );
  }

  return (
    <div className="letters-page">
      {note ? (
        <div
          className="note"
          dangerouslySetInnerHTML={{ __html: note.colorRedWord(font) }}
        ></div>
      ) : (
        ""
      )}
      {lettersCon}
    </div>
  );
}
